*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Forum", "Times New Roman", serif;
  color: #ffffff;
  background-color: #3b181e;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

textarea {
  resize: none;
}

*:focus {
  outline: none;
  box-shadow: 0 0 4px 4px #fff;
}

@media (max-width: 767px) {
  *:focus {
    box-shadow: none;
  }
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}

input::placeholder,
textarea::placeholder {
  opacity: 1;
}

input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.scroll-lock {
  position: fixed;
  overflow-y: auto;
  /* overflow: hidden;
  -webkit-overflow-scrolling: none;

  overscroll-behavior: none;
  touch-action: none; */
}

.btn-reset {
  padding: 0;
  background: rgba(0, 0, 0, 0.4);
  border: none;
  cursor: pointer;
}

body::-webkit-scrollbar {
  width: 5px;
  background-color: #1a0c06;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.8);
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 95px;
}

@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}

@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}

@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

@media (max-width: 767px) {
  .container--no-padding {
    padding: 0;
  }
}

.container--no-limit {
  max-width: 100%;
}

.btn {
  padding: 19px 30px 17px;
  border: 1px solid #ffffff;
  font-size: 12px;
  line-height: 13px;
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.4);
  transition: background-color 0.3s ease, color 0.3s ease;
}

.btn--not-opacity {
  background-color: #010001;
  border-color: transparent;
}

.btn--transparent {
  background-color: transparent;
}

.btn:hover {
  background-color: #ffffff;
  color: #000000;
}

.btn--light {
  padding: 17px 30px 16px;
  color: #010001;
  background-color: #ffffff;
}

.btn--light:hover {
  color: #ffffff;
  background-color: #010001;
}

@media (max-width: 1023px) {
  .btn {
    padding: 23px 40px 21px;
    font-size: 15px;
    line-height: 17px;
  }
}

@media (max-width: 767px) {
  .btn {
    padding: 19px 36px 18px;
    font-size: 10px;
    line-height: 11px;
  }
}

.logo {
  display: block;
}

.btn-close {
  position: absolute;
  width: 28px;
  height: 28px;
  top: 104px;
  right: 5%;
  background-color: transparent;
  transition: opacity 0.3s ease;
}

.btn-close::before, .btn-close::after {
  position: absolute;
  content: "";
  width: 38px;
  height: 2px;
  left: 0;
  background-color: #ffffff;
}

.btn-close::before {
  top: 0;
  transform: rotate(45deg) translate(5px, 13px);
}

.btn-close::after {
  bottom: 0;
  transform: rotate(-45deg) translate(5px, -13px);
}

.btn-close:hover, .btn-close:active {
  opacity: 0.3;
}

@media (max-width: 1023px) {
  .btn-close {
    right: 9%;
  }
}

@media (max-width: 767px) {
  .btn-close {
    width: 21px;
    height: 21px;
  }
  .btn-close::before, .btn-close::after {
    width: 26px;
  }
  .btn-close::before {
    transform: rotate(45deg) translate(5px, 8px);
  }
  .btn-close::after {
    transform: rotate(-45deg) translate(5px, -9px);
  }
}

.subtitle {
  position: relative;
  margin: 0 auto;
  font-size: 30px;
  line-height: 33px;
  text-align: center;
  font-weight: 400;
  color: #ffffff;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.subtitle::before {
  position: absolute;
  content: "";
  width: 2.85em;
  height: 2px;
  bottom: -22px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #ffffff;
}

@media (max-width: 767px) {
  .subtitle {
    font-size: 20px;
    line-height: 34px;
  }
}

.form {
  display: flex;
  max-width: 380px;
  flex-wrap: wrap;
  margin-left: auto;
  padding: 66px;
}

.form__appeal {
  display: block;
  max-width: 249px;
  margin: 0 auto;
  margin-bottom: 50px;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
  font-weight: 400;
  letter-spacing: 0.09em;
  text-transform: uppercase;
}

.form__label {
  width: 100%;
}

.form__label:not(:last-of-type) {
  margin-bottom: 17px;
}

.form__input {
  width: 100%;
  padding: 13px 22px;
  border: 1px solid rgba(246, 246, 249, 0.2);
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -0.01em;
  color: #ffffff;
  background-color: transparent;
}

.form__input::placeholder {
  opacity: 1;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
}

.form__submit-btn {
  width: 100%;
  margin-top: 27px;
}

@media (max-width: 1023px) {
  .form {
    max-width: 460px;
    margin: 0 auto;
    padding: 104px 44px 78px;
  }
  .form__appeal {
    max-width: 90%;
    margin-bottom: 75px;
    font-size: 30px;
    line-height: 40px;
  }
  .form__input {
    padding: 21px 33px;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: -0.01em;
  }
  .form__input::placeholder {
    font-size: 20px;
    line-height: 26px;
    letter-spacing: -0.01em;
  }
  .form__label:not(:last-of-type) {
    margin-bottom: 25px;
  }
  .form__submit-btn {
    margin-top: 41px;
  }
}

@media (max-width: 767px) {
  .form {
    width: 100%;
    padding: 0;
  }
  .form__appeal {
    margin-bottom: 50px;
    font-size: 20px;
    line-height: 30px;
  }
  .form__label:not(:last-of-type) {
    margin-bottom: 17px;
  }
  .form__input {
    padding: 14px 22px 13px;
    font-size: 16px;
    line-height: 20px;
  }
  .form__input::placeholder {
    font-size: 16px;
    line-height: 20px;
  }
  .form__submit-btn {
    margin-top: 27px;
    padding: 15px 36px 14px;
    font-size: 11px;
    line-height: 12px;
  }
}

.header {
  width: 100%;
  background-color: transparent;
  position: absolute;
  z-index: 50;
}

.header__container {
  max-width: 100%;
}

@media (max-width: 767px) {
  .header {
    position: fixed;
    background-color: #3b181e;
  }
  .header--color {
    background-color: #3b181e;
  }
  .header__container {
    padding: 0 30px;
  }
}

.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 87px 0;
}

.menu__social-list,
.menu__nav-list,
.menu__close-btn,
.menu__nav-logo {
  display: none;
}

.menu__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu__inner {
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  min-width: 100vw;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 95px;
  overflow-y: auto;
  background-color: #3b181e;
  transform: translateX(100vw);
  transition: transform 0.5s;
  z-index: 100;
}

.menu__inner::-webkit-scrollbar {
  width: 5px;
  background-color: #1a0c06;
}

.menu__inner::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: white;
}

.menu__inner .menu__nav {
  width: 100%;
  margin: 0 auto;
}

.menu__inner .menu__nav-logo {
  display: block;
  margin-bottom: 200px;
}

.menu__inner .logo img {
  margin: 0 auto;
}

.menu__inner .menu__nav-list {
  display: block;
}

.menu__inner .menu__nav-list .nav-list__item {
  max-width: 320px;
  margin: 0 auto;
  text-align: center;
}

.menu__inner .menu__nav-list .nav-list__item:not(:last-child) {
  margin-bottom: 80px;
}

.menu__inner .menu__nav-list .nav-list__item--active {
  transform: scale(1.3);
}

.menu__inner .menu__nav-list .nav-list__item--active .nav-list__link {
  color: white;
}

.menu__inner .menu__nav-list .nav-list__link {
  display: block;
  font-size: 35px;
  line-height: 39px;
  text-transform: uppercase;
}

.menu__inner .menu__social-list {
  display: block;
  margin-top: -20%;
}

.menu__inner .menu__close-btn {
  display: block;
  top: 104px;
  right: 5%;
}

.menu__inner--open {
  min-width: 100vw;
  transform: translateX(0);
  transition: transform 1s;
}

.menu__toggle {
  transition: opacity 0.3s ease;
}

.menu__toggle--hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

@media (max-width: 1439px) {
  .menu {
    padding: 30px 0;
  }
}

@media (max-width: 1023px) {
  .menu {
    align-items: flex-start;
  }
  .menu__inner .menu__close-btn {
    top: 116px;
  }
  .menu__inner--open {
    padding: 112px;
  }
  .menu__inner--open .logo {
    margin-bottom: 80px;
  }
  .menu__inner--open .menu__nav-list .nav-list__item:not(:last-child) {
    margin-bottom: 70px;
  }
  .menu__inner--open .menu__nav-list .nav-list__link {
    font-size: 30px;
    line-height: 33px;
  }
  .menu__inner--open .menu__social-list {
    display: none;
  }
}

@media (max-width: 767px) {
  .menu {
    padding: 15px 0 20px;
    align-items: center;
  }
  .logo img {
    width: auto;
  }
  .menu__inner .menu__close-btn {
    top: 25px;
  }
  .menu__inner--open {
    padding: 26px;
  }
  .menu__inner--open .logo {
    margin-bottom: 60px;
  }
  .menu__inner--open .menu__nav-list .nav-list__item {
    max-width: 180px;
  }
  .menu__inner--open .menu__nav-list .nav-list__item:not(:last-child) {
    margin-bottom: 51px;
  }
  .menu__inner--open .menu__nav-list .nav-list__link {
    font-size: 15px;
    line-height: 17px;
    letter-spacing: 0.09em;
  }
}

.menu-toggle {
  display: grid;
  height: 22px;
  grid-template-rows: 1fr;
  grid-template-columns: 23px 1fr;
  gap: 15px;
  align-items: center;
  background-color: transparent;
  transition: opacity 0.3s ease;
}

.menu-toggle:hover, .menu-toggle:active {
  opacity: 0.3;
}

.menu-toggle__line {
  position: relative;
  width: 100%;
  height: 100%;
}

.menu-toggle__line::before, .menu-toggle__line::after {
  position: absolute;
  content: "";
  width: 23px;
  height: 1px;
  top: 8px;
  left: 0;
  background-color: #ffffff;
}

.menu-toggle__line::after {
  top: auto;
  bottom: 8px;
}

.menu-toggle__text {
  font-size: 20px;
  line-height: 22px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.06em;
}

@media (max-width: 1023px) {
  .menu-toggle {
    height: 8px;
    width: 35px;
    grid-template-columns: 1fr;
  }
  .menu-toggle__line::before, .menu-toggle__line::after {
    width: 100%;
  }
  .menu-toggle__text {
    display: none;
  }
}

@media (max-width: 1023px) {
  .catalog-slider .catalog-frames {
    width: 100%;
    flex-wrap: nowrap;
  }
  .catalog-slider .catalog-slider__pagination {
    display: flex;
    margin-top: 20px;
    justify-content: center;
  }
  .catalog-slider .swiper-pagination-bullets .swiper-pagination-bullet {
    width: 9.4%;
    height: 3px;
    margin: 0 6px;
    border-radius: 50px;
    background-color: #ffffff;
    opacity: 0.3;
  }
  .catalog-slider .swiper-pagination-bullets .swiper-pagination-bullet-active {
    opacity: 1;
  }
}

@media (max-width: 767px) {
  .catalog-slider .catalog-frames__item.swiper-slide {
    width: 100%;
  }
  .catalog-slider .catalog-slider__pagination {
    justify-content: space-between;
  }
  .catalog-slider .swiper-pagination-bullets .swiper-pagination-bullet {
    width: 21.3%;
    margin: 0;
  }
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav-list--row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.nav-list__item:hover .nav-list__link {
  color: white;
}

.nav-list__link {
  display: block;
  color: rgba(255, 255, 255, 0.3);
  transition: color 0.3s ease;
}

.nav-list__link--active {
  color: white;
}

.social-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.social-list__item:not(:last-child) {
  margin-bottom: 24px;
}

.social-list--row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.social-list a {
  display: flex;
