.content {
  display: flex;
  flex-direction: column;
  flex-grow: 2;
  padding-right: 50px;
  padding-left: 111px;
  padding-bottom: 45px;
  padding-top: clamp(200px, 23vh, 350px);
}

.content h1 {
  max-width: 510px;
  margin: 0;
  margin-bottom: clamp(37px, 6vh, 70px);
  font-size: 50px;
  line-height: 55px;
  font-weight: 400;
  letter-spacing: 0.09em;
  text-transform: uppercase;
}

.content p {
  max-width: 565px;
  margin: 0;
  margin-bottom: clamp(25px, 4vh, 65px);
  font-size: 18px;
  line-height: 150%;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.content__more {
  display: flex;
  margin-top: auto;
  justify-content: flex-end;
  align-items: center;
}

.content__text-more {
  margin: 0;
  margin-right: 30px;
  transition: opacity 0.3s ease;
}

.content__text-more:hover, .content__text-more:active {
  opacity: 0.3;
}

@media (max-width: 1439px) {
  .content {
    padding: 30px 50px 70px;
  }
  .content h1 {
    max-width: 100%;
    margin-bottom: 23px;
    font-size: 40px;
    line-height: 44px;
  }
  .content p {
    width: 100%;
    max-width: 905px;
    font-size: 20px;
    line-height: 24px;
  }
  .content__more {
    display: none;
  }
}

@media (max-width: 767px) {
  .content {
    padding: 30px 20px 45px;
    overflow-x: hidden;
  }
  .content h1 {
    max-width: 60%;
    font-size: 20px;
    line-height: 22px;
  }
  .content p {
    font-size: 18px;
    line-height: 22px;
  }
}

.services-slider {
  position: relative;
  max-width: 546px;
  margin-bottom: clamp(120px, 12vh, 120px);
}

.services-slider .services-slider__btn {
  width: 48px;
  height: 48px;
  transition: opacity 0.3s ease;
}

.services-slider .services-slider__btn--prev {
  left: -26px;
}

.services-slider .services-slider__btn--prev::after {
  transform: rotate(180deg);
}

.services-slider .services-slider__btn--next {
  right: -25px;
}

.services-slider .services-slider__btn::after {
  content: "";
  width: 48px;
  height: 48px;
  background-image: url("../img/arrow-slider.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.services-slider .services-slider__btn.swiper-button-disabled {
  opacity: 0;
}

.services-slider .services-slider__btn:hover, .services-slider .services-slider__btn:active {
  opacity: 0.5;
}

@media (max-width: 1439px) {
  .services-slider {
    order: -1;
    width: 100%;
    max-width: 668px;
    margin-bottom: 70px;
  }
  .services-slider .swiper-slide img {
    width: 145px;
    height: auto;
  }
  .services-slider__btn--next {
    right: -19px;
  }
}

@media (max-width: 767px) {
  .services-slider {
    order: 0;
    width: 100%;
    max-width: 352px;
    margin-bottom: 0;
  }
  .services-slider .swiper-slide {
    width: 75px;
    height: auto;
  }
  .services-slider .services-slider__btn {
    transform: scale(0.7) translateY(-10%);
  }
  .services-slider .services-slider__btn--prev {
    left: -25px;
  }
  .services-slider .services-slider__btn--next {
    right: -19px;
  }
}

